import React from "react";

const Header = () => {
  const smoothScroll = (targetId) => {
    const targetElement = document.getElementById(targetId);
    if (targetElement) {
      window.scrollTo({
        top: targetElement.offsetTop,
        behavior: "smooth",
      });
    }
  };

  return (
    <>
    {/* Manual Classes */}
      {/* <div className="nav-header">
        <p className="logo-web">Hi, I'm Danang Wahyu Nugroho</p>
        <ul className="nav-list">
          <li className="nav-item">
            <button onClick={() => smoothScroll('about')}>About Me</button>
          </li>
          <li className="nav-item">
            <button onClick={() => smoothScroll('skill')}>Skill</button>
          </li>
          <li className="nav-item">
            <button onClick={() => smoothScroll('projects')}>Projects</button>
          </li>
          <li className="nav-item">
            <button onClick={() => smoothScroll('contact')}>Contact</button>
          </li>
        </ul>
      </div> */}
    
    {/* Tailwind Classes */}
    <div className="flex justify-between items-center px-5 w-full">
        <p className="text-sm font-extrabold hidden sm:block">Hi, I'm Danang Wahyu Nugroho</p>
        <ul className="flex flex-row gap-1 p-3 w-[67vh]">
          <li className="w-full h-full p-2 text-center cursor-pointer rounded-xl hover:bg-slate-600 items-center transition" onClick={() => smoothScroll('about')}>
            <span className="text-lg font-semibold transition" >About Me</span>
          </li>
          <li className="w-full h-full p-2 text-center cursor-pointer rounded-xl hover:bg-slate-600 items-center transition" onClick={() => smoothScroll('skill')}>
            <span className="text-lg font-semibold transition" >Skills</span>
          </li>
          <li className="w-full h-full p-2 text-center cursor-pointer rounded-xl hover:bg-slate-600 items-center transition" onClick={() => smoothScroll('projects')}>
            <span className="text-lg font-semibold transition" >Projects</span>
          </li>
          <li className="w-full h-full p-2 text-center cursor-pointer rounded-xl hover:bg-slate-600 items-center transition" onClick={() => smoothScroll('contact')}>
            <span className="text-lg font-semibold transition" >Contact</span>
          </li>
        </ul>
      </div>
    </>
  );
};

export default Header;
