import React from "react";
import { FloatButton, Progress } from "antd";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCss3, faFacebook, faGithub, faGitlab, faHtml5, faInstagram, faJs, faLinkedin, faNodeJs, faReact, faWhatsapp } from "@fortawesome/free-brands-svg-icons";
import { faCircleInfo, faX } from "@fortawesome/free-solid-svg-icons";

// Import Component
import Header from "./components/header";
import Projects from "./components/projects";
import people from "./assets/img/people.webp"
import keyboard from "./assets/img/keyboard-2.jpg"
import icons from "./components/icons";
import { Navigate } from "react-router-dom";

const App = () => {

    return (
        <>
        <Header />
        {/* Perkenalan / About Me */}
            <div id="about" className="w-full h-auto lg:h-[100vh] grid sm:grid-cols-1 md:grid-cols-2 items-center relative p-5 img-head bg-cover bg-no-repeat z-20">
                <div className="flex justify-center items-center relative">
                    <img src={people} alt="Ini Gambar" className="h-[60vh]" />
                </div>
                <div className="pr-5 text-white relative">
                    <h2 className="text-2xl font-bold">Hi!!, My name is Danang Wahyu Nugroho</h2>
                    <h3 className="text-xl font-semibold pt-2">I'm a FullStack Web Developer (Heavy In Backend)</h3>
                    <p className="text-base font-semibold pt-5">
                    As a Fullstack web developer with 3 years of experience, I specialize in building React applications. 
                    My core competency lies in end-to-end complete management of new project development, 
                    with a focus on custom-tailored apps and services for businesses. I already make 3+ project involving ERP System, 
                    so i confident of my knowledge about ERP System.
                    </p>
                </div>
            </div>

        {/* Keahlian / Skill */}
        <div id="skill" className="flex flex-col justify-center items-center h-auto lg:h-[90vh] bg-white p-14">
            <h2 className="text-5xl font-bold">Skills</h2>
            <div className="rounded-xl bg-white p-14 mt-3">
                <div className="grid sm:grid-cols-4 md:grid-cols-6 lg:grid-cols-10 grid-cols-3 gap-5">
                    <img src={icons.html5} className="h-20 w-20"/>
                    <img src={icons.css} className="h-20 w-20"/>
                    <img src={icons.js} className="h-20 w-20"/>
                    <img src={icons.typescript} className="h-20 w-20"/>
                    <img src={icons.tailwind} className="h-20 w-20"/>
                    <img src={icons.bootstrap} className="h-20 w-20"/>
                    <img src={icons.php} className="h-20 w-20"/>
                    <img src={icons.react} className="h-20 w-20"/>
                    <img src={icons.next} className="h-20 w-20"/>
                    <img src={icons.electron} className="h-20 w-20"/>
                    <img src={icons.nodejs} className="h-20 w-20"/>
                    <img src={icons.graphql} className="h-20 w-20"/>
                    <img src={icons.express} className="h-20 w-20"/>
                    <img src={icons.laravel} className="h-20 w-20"/>
                    <img src={icons.kotlin} className="h-20 w-20"/>
                    <img src={icons.mysql} className="h-20 w-20"/>
                    <img src={icons.mongodb} className="h-20 w-20"/>
                    <img src={icons.sqlite} className="h-20 w-20"/>
                    <img src={icons.git} className="h-20 w-20"/>
                    <img src={icons.github} className="h-20 w-20"/>
                    <img src={icons.gitlab} className="h-20 w-20"/>
                </div>
            </div>
        </div>

        {/* Projects */}
        <div id="Project" className="flex flex-col relative justify-center items-center h-auto lg:min-h-[90vh] text-white p-14 bg-slate-800 overflow-hidden">
            <h2 className="text-5xl font-bold mb-4">Projects </h2>
            <Projects />
        </div>

        {/* Contact */}
        <div id="contact" className="grid grid-cols-2 items-center min-h-[20vh] max-h-[60vh] bg-slate-900 text-white relative p-5">
            <div className="flex flex-col">
                <h2 className="text-3xl font-bold mb-4">Contact Me</h2>
                <p style={{position: "absolute", bottom: "0"}}>Copyright &copy; 2023 Danang</p>
            </div>
            <div>
                <h4 className="text-xl font-bold">You can contact me in this social media</h4>
                <div className="flex flex-row gap-4 mt-4">
                    <button className="hover:bg-slate-600 p-2 rounded-full transition duration-300" onClick={() => window.open('https://wa.me/6289509918979', '_blank')}><FontAwesomeIcon icon={faWhatsapp} size="2xl"/></button>
                    <button className="hover:bg-slate-600 p-2 rounded-full transition duration-300" onClick={() => window.open('https://linkedin.com/in/danang-wahyu-nugroho', '_blank')}><FontAwesomeIcon icon={faLinkedin} size="2xl"/></button>
                    <button className="hover:bg-slate-600 p-2 rounded-full transition duration-300" onClick={() => window.open('https://www.instagram.com/danang130603/', '_blank')}><FontAwesomeIcon icon={faInstagram} size="2xl"/></button>
                </div>
            </div>
        </div>


        <FloatButton.Group shape="circle" trigger="hover" tooltip="Informasi" icon={<FontAwesomeIcon icon={faCircleInfo} />}>
            <FloatButton icon={<FontAwesomeIcon icon={faWhatsapp} />} tooltip="Whatsapp" onClick={() => window.open('https://wa.me/6289509918979', '_blank')}/>
            <FloatButton icon={<FontAwesomeIcon icon={faLinkedin} />} tooltip="Linkedin" onClick={() => window.open('https://linkedin.com/in/danang-wahyu-nugroho', '_blank')}/>
            <FloatButton icon={<FontAwesomeIcon icon={faInstagram} />} tooltip="Instagram" onClick={() => window.open('https://www.instagram.com/danang130603/', '_blank')}/>
            <FloatButton.BackTop visibilityHeight={1} />
        </FloatButton.Group>
        </>
    )
}

export default App;